.auth-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  min-height: calc(100vh - 70px);
}

.auth-header-content {
  display: flex;
  flex-direction: column;
  margin-bottom: 28px;
}

.auth-go-signup {
  text-align: center;
}

.sign-up-form-left {
  margin-right: 30px;
  width: 320px;
}

.sign-up-form-right {
  width: 320px;
}

@media screen and (max-width: 768px) {
  .sign-up-form-left {
    margin-right: 0px;
    width: auto;
  }

  .sign-up-form-right {
    width: auto;
  }
}
